<template>
  <v-text-field
    :ref="id"
    :id="id"
    v-model="valueModel"
    :dark="dark"
    :value="valueModel"
    :label="label"
    :disabled="disabled"
    :rules="rules"
    :hide-details="hideDetails"
    :counter="counter"
    :placeholder="placeholder"
    :outlined="outlined"
    :dense="dense"
    :color="success ? 'success' : 'primary'"
    :type="type"
    :solo="solo"
    :success="success"
    v-on:keyup.enter="onKeyUpEnter"
    :name="id"
    @keyup="onKeyUp"
    @keyup.enter="onEnter"
    @input="onInput"
    validate-on-blur
    required
  >
  <!--aria-autocomplete="false"
    autocomplete="off"-->
    
    <template v-slot:prepend-inner>
      <v-icon
        class="material-icons-outlined"
        :class="success ? 'success--text' : ''"
        dense
      >
        {{ icon }}
      </v-icon>
    </template>
  </v-text-field>
</template>

<script>
export default {
  name: 'InputField',
  props: {
    solo: { default: false }, 
    label: { required: true },
    value: { default: '' },
    disabled: { default: false },
    counter: { default: null },
    dense: { default: false },
    rules: { default: Array },
    id: { default: null },
    placeholder: { default: '' },
    type: { default: null },
    icon: { default: null },
    outlined: { default: false },
    dark: { default: false },
    success: { default: null },
     hideDetails: {
      default: false
    }
  },
  data: () => ({
    valueModel: ''
  }),
  mounted () {
    this.valueModel = this.value
  },
  watch: {
    value (newValue) {
      this.valueModel = newValue
    }
  },
  methods: {
    onEnter () {
      this.$emit('onEnter', this.valueModel, this.id)
    },
    onKeyUp () {
      this.$emit('onKeyUp', this.valueModel, this.id)
    },
    onKeyUpEnter () {
      this.$emit('onKeyUpEnter', this.valueModel, this.id)
    },
    onInput () {
      if (this.id) {
        setTimeout(() => { 
          const el = document.querySelector(`#${this.id}`)
          el.value = this.valueModel 
        }, 100);
      }
    }
  }
}
</script>