<template>
  <v-btn
    class="success text-capitalize btn py-4 px-8 elevation-0"
    :class="cssClass"
    height="auto"
    :block="block"
    :disabled="disabled"
    @click="clicked"
  >
    <template v-if="loading">
      <v-progress-circular
        indeterminate
        width="2"
        color="primaryDark"
        small
      ></v-progress-circular>
    </template>
    <template v-else>
      <v-icon
        v-if="icon"
        class="mr-1"
      >
        {{ icon }}
      </v-icon>
      {{ text }}
    </template>
  </v-btn>
</template>

<script>
export default {
  name: 'BtnSecondary',
  props: {
    block: { default: true },
    disabled: { default: false },
    text: { default: 'Confirmar' },
    icon: { default: '' },
    cssClass: { default: 'py-3' },
    loading: { default: false }
  },
  methods: {
    clicked () {
      this.$emit('clicked')
    }
  }
}
</script>

<style scoped>
.btn {
  color: #4F4F4F !important;
  border-radius: 45px;
  font-weight: 600;
}
</style>