<template>
  <v-text-field
    v-model="valueModel"
    :id="id"
    :label="label"
    :dense="dense"
    :disabled="disabled"
    :dark="dark"
    :rules="rules"
    :value="valueModel"
    :counter="counter"
    :solo="solo"
    :success="success"
    :outlined="outlined"
    :placeholder="placeholder"
    :hide-details="hideDetails"
    color="success"
    v-mask="'(##) # ####-####'"
    @keyup="onKeyUp"
    validate-on-blur
    required
    aria-autocomplete="false"
    autocomplete="off"
  >
    <template v-slot:prepend-inner>
      <v-icon
        class="material-icons-outlined"
        :class="success ? 'success--text' : ''"
        dense
      >
        {{ icon }}
      </v-icon>
    </template>
  </v-text-field>
</template>

<script>
import {mask} from 'vue-the-mask'

export default {
  name: 'PhoneField',
  directives: {mask},
  props: {
    dense: { default: false },
    solo: { default: false },
    dark: { default: false },
    label: {
      required: true
    },
    placeholder: {
      default: null
    },
    value: {
      default: ''
    },
    disabled: {
      default: false
    },
    counter: {
      default: null
    },
    required: {
      default: false
    },
    id: {
      default: null
    },
    icon: {
      default: null
    },
    success: {
      default: null
    },
    outlined: {
      default: false
    },
    hideDetails: {
      default: false
    }
  },
  data: () => ({
    valueModel: null
  }),
  computed: {
    rules () {
      if (!this.required) return []
      return [v => (v && v.length >= 15) || 'Informe um número válido']
    }
  },
  watch: {
    value (newValue) {
      this.valueModel = newValue
    }
  },
  mounted () {
    this.valueModel = this.value
  },
  methods: {
    onKeyUp () {
      this.$emit('onKeyUp', this.valueModel, this.id)
    }
  }
}
</script>